import React from 'react'
import Buttons from './Buttons';

function Header({title , onadd,showadd}) {

   
    return (
        <div className = 'header'>
            {title}
            <Buttons color={showadd ? 'red' : 'green'} text={showadd ? 'close' : 'Add'} onClick = {onadd}/>
            
        </div>

    )
}
Header.defaultProps = {
    title: 'dax patel'
}

export default Header
